@use 'root-rhythm';
@use 'color-functions';
@use 'design-variables';
@use 'colors';
@use 'color-schemes';
@use 'typography';


button,
a.button,
input.button {
  @include root-rhythm.rr-font-compute(l, tight, typography.$fonts, typography.$lineheights);

  //TODO: Review this, this assumes S breakpoint
  // should never get 2-up buttons.
  display: block;
  width: 80%;
  margin:root-rhythm.rr-gridbase(1) auto;

  @include root-rhythm.rr-break-directive(m) {
    display: inline-block;
    width:auto;
    margin: 0;
  }

  //  Avenir is not quite centered in the lineheight
  //  because of weird font metrics, so in order to get
  //  text visibly aligned I have to provide unique padding
  //  values for top and bottom.
  padding: {
    top: calc(1rem - 2px);
    right: calc(1.875rem - 3px);
    bottom: calc(1rem - 4px);
    left: calc(1.875rem - 3px);
  }
    transition: .3s ease-in-out;
    border-width: 2px;
    border-color: var(--button-border);
    background-color: var(--button-background);
    color: var(--button-text);
    border-style: solid;
    font-family: typography.$font-usual;
    font-weight: 500;
    text-align: center;
    text-decoration: none;
    text-transform: uppercase;

    &:link,
    &:visited {
      border-color: var(--button-border);
      background-color: var(--button-background);
      color: var(--button-text);
    }

  &[disabled] {
    // sass-lint:disable-block no-important
    border-color: var(--button-disabled-border) !important;
    background-color: var(--button-disabled-background) !important;
    color: var(--button-disabled-text) !important;

    &.button--ghost {
      background-color: transparent !important;
      // The ghost disabled text is a weird case; we have to
      // tune it for specific text schemes
      // @if $textscheme == color-schemes.$scheme-dark {
      //   color: var(--color-wildcat-white) !important;
      // } @else {
        color: var(--button-disabled-text) !important;
      // }
    }
  }

  &:hover {
    border-color: var(--button-hover-border);
    background-color: var(--button-hover-background);
    color: var(--button-hover-text);

    &.button--ghost {
      background-color: var(--button-background);
      border-color: var(--button-background);
      --button-hover-text: var(--button-hover-background);
    }
  }

  &:active,
  &[aria-pressed='true'] {
    border-color: var(--button-active-border);
    background-color: var(--button-active-background);
    color: var(--button-active-text);

    &.button--ghost {
      color: var(--button-active-text);
    }

    &.button--transparent {
      border-color: transparent;
      background-color: transparent;
      color: var(--button-active-border);
    }
  }

  &:focus {
    border-color: var(--button-focus-border);
    outline: none;
    background-color: var(--button-focus-background);
    color: var(--button-focus-text);

    &.button--ghost {
      // background-color: transparent;
      // Like disabled ghost, the focus state is quite different
      // and we need some extra tuning
      // @if $textscheme == color-schemes.$scheme-dark {
      //   border-color: var(--color-wildcat-white);
      // } @else {
        color: var(--button-focus-text);
      // }

    }
  }

  $skins: transparent;
  @each $name, $color in colors.$colors {
    &.button--#{""+$name} {
      background-color: $color;
      @if (color-functions.contrast($color, color-functions.color(wildcat-black)) < 4.5) {
        // @include design-variables.usescheme(color-schemes.$scheme-dark);
      }

      .heading--display span {
        background-color: $color;
      }
    }
    $skins: append($skins, button--#{$name});
  }

  &.button--ghost {
    background-color: transparent;
    border-color: var(--button-hover-border); // The background should be the 'opposite' of the border and text
    // color: var(--button-hover-text);

    //flipping button colors for Ghost
    --button-text: var(--button-hover-text);
    --button-border: var(--button-hover-border);

  }

  &.button--bluegrass {
    border-color: var(--color-bluegrass);
  }

  &.button--transparent {
    border-color: transparent;
    background-color: transparent;
    // color: var(--button-border);
  }


  // Variants
  &.button--small {
    @include root-rhythm.rr-font-compute(s, tight, typography.$fonts, typography.$lineheights);
    padding: {
      right: calc(1.625rem - 3px);
      left: calc(1.625rem - 3px);
    }
  }

  &.button--large {
    @include root-rhythm.rr-font-compute(l, tight, typography.$fonts, typography.$lineheights);
    padding: {
      right: calc(2rem - 3px);
      left: calc(2rem - 3px);
    }
  }
}

button.video-button {
  display: block;

  .video-button__pause {
    display: block;
  }

  .video-button__play {
    display: none;
  }

  &.video-button--paused {
    .video-button__pause {
      display: none;
    }

    .video-button__play {
      display: block;
    }
  }
}



